@import "../core/variables";

.list {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  padding-bottom: 30px;

  &__element {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 92%;
    height: 100%;
    border: 2px solid $colorSecondary;
    padding: 15px 0;
    list-style: none;
  }
}
